.Placeholder {
    display: flex;
    align-items: center;
    flex-direction: column;
    margin: calc(var(--safe-area-inset-top) + var(--panelheader_height_ios) + 100vh / 812 * 72) 28px 0 28px;
}

.Main {
    margin: calc(var(--safe-area-inset-top) + var(--panelheader_height_ios)) 28px 0 28px;
}

.Cards__Container, .Cards2__Container, .StatsCards__Container {
    justify-content: space-between;
}

.Cards__Container > *, .Cards2__Container > * {
    width: 100%;
}

.Cards__Container > :last-child, .Cards2__Container > :last-child {
    margin-left: 23px;
}

.CardButton {
    display: flex;
    margin-top: 24px;
    justify-content: center;
    align-items: center;
    padding: 14px 12px;
    background: var(--border_color);
    border-radius: 21px;
    cursor: pointer;
    color: var(--color_secondary2);
    width: calc(100% - 12px - 12px);
}

.PlayContainer {
    bottom: calc(var(--safe-area-inset-bottom) + 38px);
    left: 50%;
    -webkit-transform: translateX(-50%);
    -moz-transform: translateX(-50%);
    -ms-transform: translateX(-50%);
    -o-transform: translateX(-50%);
    transform: translateX(-50%);
    z-index: 2;
}

.Gallery, .Rating, .Likes, .Author, .Tags, .Friends, .FriendLobby, .Stats, .Shop {
    margin: 0 28px;
}

.Search {
    margin-top: 47px;
}

.Search h1 {
    margin: 48px 0;
}

.Game {
    margin: calc(var(--safe-area-inset-top) + var(--panelheader_height_ios)) 28px 0;
    display: flex;
    align-items: center;
    flex-direction: column;
}

.Comments_Container {
    width: 100%;
}

.Comment_Input {
    margin-top: 14px;
    width: calc(319px * var(--scale-value));
}

.Comments {
    max-height: 114px;
}

.Result {
    margin: calc(var(--safe-area-inset-top) + var(--panelheader_height_ios) + 5.78817734vh) 28px 0;
}

.Result .Button--lvl-secondary {
    position: absolute;
    bottom: calc(var(--safe-area-inset-bottom) + 42px);
}

.Canvas_Container {
    width: min(calc(319px * var(--scale-value)), 319px);
    height: min(calc(319px * var(--scale-value)), 319px);
    margin-top: 28px;

    background: #FFFFFF;
    box-shadow: 0px 0px 227px rgba(0, 0, 0, 0.15);
    border-radius: 28px;
    overflow: hidden;
}

.PictureInfo {
    margin: 18px 28px 0 28px;
    padding-bottom: 12px;
}

.PictureInfo .Canvas_Container .Actions_Container .LikeButton {
    width: calc(148px * var(--scale-value));
}

.PictureInfo .Author_Container {
    width: calc(319px * var(--scale-value));
}

.PictureInfo > .Actions_Container {
    margin-top: 16px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.PictureInfo > .Actions_Container > :last-child {
    margin-left: 10px;
}

.PictureInfo > .ButtonResumeDraw {
    width: 100%;
}

.Offline_Drawing__Buttons {
    width: calc(319px * var(--scale-value));
}

.vkuiPromoBanner {
    width: 88.8vw;
    left: 50%;
    -webkit-transform: translateX(-50%);
    -moz-transform: translateX(-50%);
    -ms-transform: translateX(-50%);
    -o-transform: translateX(-50%);
    transform: translateX(-50%);
    bottom: 2vh;
}

.Tags h2, .Friends h2, .FriendLobby h2, .Stats h2, .Shop h2 {
    font-family: Manrope;
    font-style: normal;
    font-weight: 600;
    font-size: 24px;
    line-height: 33px;
}

.MainTint {
    background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(40, 43, 46, 0.2) 100%);
    height: 148px;
    position: fixed;
    bottom: 0;
    width: 100vw;
    left: 0;
    right: 0;
    z-index: 1;
    pointer-events: none;
}

.CardButton > .Icon {
    margin-left: 4px;
}

.Main .Colors__Container {
    padding: 0;
    margin: 24px 0 0;
}

.Main .Colors__Container .Color:first-child, :not(.Main .Colors__Container .Color) + .Main .Colors__Container .Color {
    margin-left: 24px;
}

.Main .Colors__Container > :last-child {
    margin-right: 24px;
}